import { Injectable } from '@angular/core';
import { InMemoryDbService } from 'angular-in-memory-web-api';
import { Hero } from './heroes/hero';

@Injectable({
  providedIn: 'root',
})
export class InMemoryDataService implements InMemoryDbService {
  createDb() {
    const heroes = [
      { id: 1, name: '詹姆斯',situation:'失踪（MIA）',details:'在致远星空间站表面后与星盟短兵相接时一发刺针抢刺入了詹姆斯的喷气背包，发生了爆炸，惯性将詹姆斯弹射至太空失踪。'},
      { id: 2, name: '杰' ,situation:'失踪（MIA）',details:'连同灰队其他成员在2551年失踪，2558年确认被一位精英舰长俘虏。'},
      { id: 3, name: '李' ,situation:'失踪（MIA）',details:'可能在等离子鱼雷引爆时牺牲'},
      { id: 4, name: '约翰' ,situation:'服役中',details:'曾被列为阵亡（KIA），后回到地球，2558年10月27日，ONI对外宣称士官长阵亡于子午线星战役.'},
      { id: 5, name: '兰德尔',situation:'阵亡（KIA）',details:'失踪后确认存活，后退役，并进行了退化手术（阵亡于阿尔法利刃行动）.' },
      { id: 6, name: '所罗门' ,situation:'阵亡（KIA）',details:'接受到了星盟放出的假信号而前去调查，误入陷阱与整艘战舰同归于尽。'},
      { id: 7, name: '里昂',situation:'失踪（MIA）',details:'欧米伽小队成员，2531年曾与红队一同在阿卡迪亚星协同作战，后状态未知，杰罗姆根据资料推测三人幸存到了战后。' },
      { id: 8, name: '奥格斯特',situation:'失踪（MIA）',details:'欧米伽小队成员，2531年曾与红队一同在阿卡迪亚星协同作战，后状态未知，杰罗姆根据资料推测三人幸存到了战后。' },
    ];
    return {heroes};
  }

  // Overrides the genId method to ensure that a hero always has an id.
  // If the heroes array is empty,
  // the method below returns the initial number (11).
  // if the heroes array is not empty, the method below returns the highest
  // hero id + 1.
  genId(heroes: Hero[]): number {
    return heroes.length > 0 ? Math.max(...heroes.map(hero => hero.id)) + 1 : 11;
  }
}